'use client';

import { zodResolver } from '@hookform/resolvers/zod';
import { format, isBefore, startOfToday } from 'date-fns';
import { ptBR } from 'date-fns/locale';
import { Calendar as CalendarIcon, Clock, HelpingHand, User, Lock, Trash2, Loader2, Edit, XCircle, Save, AlertTriangle, ExternalLink } from 'lucide-react';
import React, { useState, useMemo, useEffect, useCallback } from 'react';
import { useForm } from 'react-hook-form';
import { z } from 'zod';
import { doc, onSnapshot, setDoc, deleteDoc, getFirestore } from 'firebase/firestore';


import { Button } from '@/components/ui/button';
import { Calendar } from '@/components/ui/calendar';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from '@/components/ui/dialog';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from '@/components/ui/alert-dialog';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { useToast } from '@/hooks/use-toast';
import { cn } from '@/lib/utils';
import { Label } from '@/components/ui/label';
import { WhatsappIcon } from '@/components/icons';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import Link from 'next/link';
import { firebaseApp } from '@/lib/firebase';

type Slot = {
  time: string;
  isBooked: boolean;
  bookedBy: string | null;
};

type ScheduleData = {
  scheduleDate: string; // Stored as ISO string
  slots: Slot[];
  isScheduleDefined: boolean;
  startTime: number;
  endTime: number;
  whatsAppSent?: boolean;
};

const bookingFormSchema = z.object({
  name: z.string().min(2, { message: 'O nome deve ter pelo menos 2 caracteres.' }).max(50),
});

const editBookingFormSchema = z.object({
  name: z.string().min(2, { message: 'O nome deve ter pelo menos 2 caracteres.' }).max(50),
});

const adminAuthSchema = z.object({
  password: z.string().min(1, { message: 'A senha é obrigatória.' }),
});

const SCHEDULE_DOC_ID = "currentSchedule";

export function PrayerSchedule() {
  const [scheduleDate, setScheduleDate] = useState<Date | undefined>(new Date());
  const [slots, setSlots] = useState<Slot[]>([]);
  const [selectedSlot, setSelectedSlot] = useState<Slot | null>(null);
  const [isBookingDialogOpen, setIsBookingDialogOpen] = useState(false);
  const [isAdminMode, setIsAdminMode] = useState(false);
  const [isAuthDialogOpen, setIsAuthDialogOpen] = useState(false);
  const [isScheduleDefined, setIsScheduleDefined] = useState(false);
  const [startTime, setStartTime] = useState(6);
  const [endTime, setEndTime] = useState(18);
  const [whatsAppSent, setWhatsAppSent] = useState(false);
  const [whatsAppNumber, setWhatsAppNumber] = useState('5577991395904');
  const [isDeleteDialogOpen, setIsDeleteDialogOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [editingSlot, setEditingSlot] = useState<Slot | null>(null);
  const [isEditingDialogOpen, setIsEditingDialogOpen] = useState(false);
  const [isPersistenceError, setIsPersistenceError] = useState(false);

  const { toast } = useToast();
  
  const bookingForm = useForm<z.infer<typeof bookingFormSchema>>({
    resolver: zodResolver(bookingFormSchema),
    defaultValues: { name: '' },
  });

  const editBookingForm = useForm<z.infer<typeof editBookingFormSchema>>({
    resolver: zodResolver(editBookingFormSchema),
    defaultValues: { name: '' },
  });

  const authForm = useForm<z.infer<typeof adminAuthSchema>>({
    resolver: zodResolver(adminAuthSchema),
    defaultValues: { password: '' },
  });

  const db = getFirestore(firebaseApp);
  const scheduleDocRef = useMemo(() => doc(db, "prayerSchedules", SCHEDULE_DOC_ID), [db]);
  const configDocRef = useMemo(() => doc(db, "configs", "whatsApp"), [db]);

  useEffect(() => {
    setIsLoading(true);
    const unsubscribeSchedule = onSnapshot(scheduleDocRef, (docSnap) => {
      if (docSnap.exists()) {
        const scheduleData = docSnap.data() as ScheduleData;
        const savedDate = new Date(scheduleData.scheduleDate);

        if (isBefore(savedDate, startOfToday())) {
          deleteDoc(scheduleDocRef);
          setIsScheduleDefined(false);
          setScheduleDate(new Date());
          setSlots([]);
        } else {
          setScheduleDate(savedDate);
          setSlots(scheduleData.slots || []);
          setIsScheduleDefined(scheduleData.isScheduleDefined);
          setStartTime(scheduleData.startTime);
          setEndTime(scheduleData.endTime);
          setWhatsAppSent(scheduleData.whatsAppSent || false);
        }
      } else {
        setIsScheduleDefined(false);
        setScheduleDate(new Date());
        setSlots([]);
      }
      setIsLoading(false);
    }, (error) => {
      console.error("Firestore schedule subscription error:", error);
      toast({
        title: "Erro de Conexão",
        description: "Não foi possível carregar a escala de oração. Verifique sua conexão.",
        variant: "destructive"
      });
      setIsPersistenceError(true);
      setIsLoading(false);
    });

    const unsubscribeConfig = onSnapshot(configDocRef, (docSnap) => {
      if (docSnap.exists()) {
        setWhatsAppNumber(docSnap.data().number || '5577991395904');
      }
    }, (error) => {
       console.error("Firestore config subscription error:", error);
       toast({
        title: "Erro de Conexão",
        description: "Não foi possível carregar as configurações.",
        variant: "destructive"
      });
       setIsPersistenceError(true);
    });

    return () => {
      unsubscribeSchedule();
      unsubscribeConfig();
    };
  }, [scheduleDocRef, configDocRef, toast]);

  const updateAndSaveSchedule = useCallback(async (updatedData: Partial<ScheduleData>) => {
    const currentState = {
      scheduleDate: scheduleDate?.toISOString() || new Date().toISOString(),
      slots,
      isScheduleDefined,
      startTime,
      endTime,
      whatsAppSent,
    };
    const newState = { ...currentState, ...updatedData };
    
    if (newState.scheduleDate) setScheduleDate(new Date(newState.scheduleDate));
    if (newState.slots) setSlots(newState.slots);
    if (newState.isScheduleDefined !== undefined) setIsScheduleDefined(newState.isScheduleDefined);
    if (newState.startTime !== undefined) setStartTime(newState.startTime);
    if (newState.endTime !== undefined) setEndTime(newState.endTime);
    if (newState.whatsAppSent !== undefined) setWhatsAppSent(newState.whatsAppSent);
    
    try {
       await setDoc(scheduleDocRef, {
        scheduleDate: newState.scheduleDate,
        slots: newState.slots,
        isScheduleDefined: newState.isScheduleDefined,
        startTime: newState.startTime,
        endTime: newState.endTime,
        whatsAppSent: newState.whatsAppSent,
      }, { merge: true });
    } catch (error) {
      console.error("Failed to save state to Firestore", error);
      toast({
        title: "Erro de Sincronização",
        description: "Não foi possível salvar os dados. Verifique as regras de segurança do Firestore.",
        variant: "destructive",
      });
      setIsPersistenceError(true);
    }
  }, [scheduleDate, slots, isScheduleDefined, startTime, endTime, whatsAppSent, scheduleDocRef, toast]);

  const updateWhatsAppConfigInFirestore = async (newNumber: string) => {
    try {
      await setDoc(configDocRef, { number: newNumber });
      setWhatsAppNumber(newNumber);
      toast({
          title: "Número Salvo!",
          description: "O número do WhatsApp foi atualizado com sucesso."
      });
    } catch (error) {
        console.error("Failed to save WhatsApp config to Firestore", error);
        toast({
            title: "Erro de Sincronização",
            description: "Não foi possível salvar o número do WhatsApp.",
            variant: "destructive",
        });
        setIsPersistenceError(true);
    }
  };

  const generateTimeSlots = useCallback((start: number, end: number, existingSlots: Slot[] = []): Slot[] => {
    const newSlots: Slot[] = [];
    if (end <= start) return [];
  
    const bookedSlotsMap = new Map(
      existingSlots.filter(s => s.isBooked).map(s => [s.time, s])
    );
  
    for (let i = start; i < end; i++) {
        const startTimeStr = `${String(i).padStart(2, '0')}h`;
        const endTimeStr = `${String(i + 1).padStart(2, '0')}h`;
        const time = `${startTimeStr} - ${endTimeStr}`;
  
      const existingBooking = bookedSlotsMap.get(time);
  
      if (existingBooking) {
        newSlots.push(existingBooking);
      } else {
        newSlots.push({
          time: time,
          isBooked: false,
          bookedBy: null,
        });
      }
    }
    return newSlots;
  }, []);

  const bookedSlots = useMemo(() => (slots || []).filter((s) => s.isBooked).sort((a, b) => a.time.localeCompare(b.time)), [slots]);
  const allSlotsBooked = useMemo(() => isScheduleDefined && (slots || []).length > 0 && (slots || []).every((s) => s.isBooked), [slots, isScheduleDefined]);

  const handleSendToWhatsApp = useCallback(() => {
    if (!scheduleDate || bookedSlots.length === 0) return;
    const dateToFormat = new Date(scheduleDate);
    if (isNaN(dateToFormat.getTime())) {
        toast({ title: "Data inválida para envio.", variant: "destructive" });
        return;
    }
    if (!whatsAppNumber) {
        toast({
            title: "Número do WhatsApp não configurado",
            description: "Por favor, configure o número do WhatsApp na área do administrador.",
            variant: "destructive"
        });
        return;
    }
    const scheduleText = `*Escala de Oração - ${format(dateToFormat, 'PPP', { locale: ptBR })}*\n\n${bookedSlots
      .map((s) => `*${s.time}*: ${s.bookedBy}`)
      .join('\n')}\n\nObrigado a todos pela participação!`;
    const encodedMessage = encodeURIComponent(scheduleText);
    const whatsappUrl = `https://wa.me/${whatsAppNumber}?text=${encodedMessage}`;
    window.open(whatsappUrl, '_blank');
  }, [scheduleDate, bookedSlots, whatsAppNumber, toast]);

  useEffect(() => {
    if (allSlotsBooked && !whatsAppSent) {
      handleSendToWhatsApp();
      updateAndSaveSchedule({ whatsAppSent: true });
    }
  }, [allSlotsBooked, whatsAppSent, handleSendToWhatsApp, updateAndSaveSchedule]);

  const handleSelectSlot = (slot: Slot) => {
    if (!slot.isBooked) {
      setSelectedSlot(slot);
      setIsBookingDialogOpen(true);
      bookingForm.reset();
    }
  };
  
  const handleEditSlot = (slot: Slot) => {
    setEditingSlot(slot);
    editBookingForm.setValue("name", slot.bookedBy || "");
    setIsEditingDialogOpen(true);
  };

  const handleBookingSubmit = async (values: z.infer<typeof bookingFormSchema>) => {
    if (selectedSlot) {
      const updatedSlots = slots.map((s) =>
        s.time === selectedSlot.time ? { ...s, isBooked: true, bookedBy: values.name } : s
      );
      await updateAndSaveSchedule({ slots: updatedSlots });
      setIsBookingDialogOpen(false);
      setSelectedSlot(null);
      toast({
        title: 'Horário Agendado!',
        description: `Obrigado, ${values.name}. Sua hora de oração foi confirmada.`,
        className: 'bg-green-600 text-white',
      });
    }
  };
  
  const handleEditBookingSubmit = async (values: z.infer<typeof editBookingFormSchema>) => {
    if (editingSlot) {
      const updatedSlots = slots.map((s) =>
        s.time === editingSlot.time ? { ...s, isBooked: true, bookedBy: values.name } : s
      );
      await updateAndSaveSchedule({ slots: updatedSlots });
      toast({
        title: 'Agendamento Atualizado!',
        description: `O horário de ${editingSlot.time} foi atualizado para ${values.name}.`,
      });
      setIsEditingDialogOpen(false);
      setEditingSlot(null);
    }
  };
  
  const handleFreeSlot = async (slotToFree: Slot) => {
    const updatedSlots = slots.map((s) =>
      s.time === slotToFree.time ? { ...s, isBooked: false, bookedBy: null } : s
    );
     await updateAndSaveSchedule({ slots: updatedSlots });
     toast({
       title: 'Horário Liberado!',
       description: `O horário ${slotToFree.time} está disponível novamente.`,
     });
  };

  const handleAdminAuthSubmit = (values: z.infer<typeof adminAuthSchema>) => {
    if (values.password === '123') {
      setIsAuthDialogOpen(false);
      setIsAdminMode(true);
      authForm.reset();
    } else {
      toast({
        title: 'Senha Incorreta',
        description: 'A senha de administrador está incorreta.',
        variant: 'destructive',
      });
      authForm.reset();
    }
  };

  const handleAdminConfigSubmit = async () => {
    const currentScheduleDate = scheduleDate ? new Date(scheduleDate) : new Date();
    if (isNaN(currentScheduleDate.getTime())) {
      toast({ title: "Data inválida.", description: "Por favor, selecione uma data válida.", variant: "destructive" });
      return;
    }
    if (startTime >= endTime) {
      toast({ title: "Intervalo de horário inválido.", description: "O horário de início deve ser menor que o de fim.", variant: "destructive" });
      return;
    }
    const newSlots = generateTimeSlots(startTime, endTime, slots);
    const newScheduleData: Partial<ScheduleData> = {
      scheduleDate: currentScheduleDate.toISOString(),
      slots: newSlots,
      startTime,
      endTime,
      isScheduleDefined: true,
      whatsAppSent: false,
    };
    await updateAndSaveSchedule(newScheduleData);
    toast({
      title: "Agenda Definida!",
      description: `A escala para ${format(currentScheduleDate, 'PPP', { locale: ptBR })} das ${startTime}h às ${endTime}h está disponível.`
    });
  };

  const handleDeleteSchedule = useCallback(async () => {
    try {
        await deleteDoc(scheduleDocRef);
        setIsDeleteDialogOpen(false);
        setIsAdminMode(false);
        setIsScheduleDefined(false);
        setScheduleDate(new Date());
        setSlots([]);
        setStartTime(6);
        setEndTime(18);
        setWhatsAppSent(false);
        toast({
            title: "Escala Excluída",
            description: "A escala de oração foi removida com sucesso.",
        });
    } catch (error) {
        console.error("Failed to delete schedule from Firestore", error);
        toast({
            title: "Erro ao Excluir",
            description: "Não foi possível remover a escala do banco de dados.",
            variant: "destructive",
        });
    }
  }, [scheduleDocRef, toast]);

  const handleAdminButtonClick = () => {
    authForm.reset();
    setIsAuthDialogOpen(true);
  };

  return (
    <div>
      {isLoading ? (
        <div className="flex flex-col items-center justify-center min-h-screen space-y-4">
          <Loader2 className="w-16 h-16 animate-spin text-primary" />
          <p className="text-lg text-muted-foreground">Carregando escala de oração...</p>
        </div>
      ) : isAdminMode ? (
        // Admin View
        <div className="space-y-8">
            <Card className="shadow-lg">
                <CardHeader>
                <CardTitle className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                    <Lock className="w-6 h-6" />
                    Área do Administrador
                    </div>
                    <Button variant="outline" size="sm" onClick={() => setIsAdminMode(false)}>
                    Sair
                    </Button>
                </CardTitle>
                <CardDescription>
                    Configure a escala de oração, o número de WhatsApp e gerencie os agendamentos.
                </CardDescription>
                </CardHeader>
            </Card>
            {isPersistenceError && (
                <Alert variant="destructive">
                    <AlertTriangle className="h-4 w-4" />
                    <AlertTitle>Ação Necessária: Regras de Segurança do Firebase</AlertTitle>
                    <AlertDescription>
                        <p className='font-bold'>O erro "Missing or insufficient permissions" indica que as regras do seu banco de dados estão bloqueando o acesso.</p>
                        <p className='mt-2'>Para corrigir, acesse seu projeto no <a href="https://console.firebase.google.com/" target="_blank" rel="noopener noreferrer" className='underline'>Console do Firebase</a>, vá para <strong>Firestore Database</strong> &gt; <strong>Regras</strong> e substitua o conteúdo pelas seguintes regras:</p>
                        <pre className='mt-2 p-2 bg-gray-700 text-white rounded-md text-xs overflow-x-auto'>
                            {`rules_version = '2';
service cloud.firestore {
    match /databases/{database}/documents {
        match /prayerSchedules/{scheduleId} {
            allow read, write: if true;
        }
        match /configs/{configId} {
            allow read, write: if true;
        }
    }
}`}
                        </pre>
                    </AlertDescription>
                </Alert>
            )}
            <Card className="shadow-lg">
                <CardHeader>
                <CardTitle>Configuração do WhatsApp</CardTitle>
                <CardDescription>
                    Insira o número de WhatsApp para onde a escala será enviada. Use o formato internacional sem `+' ou espaços (ex: 5511999998888).
                </CardDescription>
                </CardHeader>
                <CardContent>
                <div className="flex items-center gap-2">
                    <WhatsappIcon className="w-5 h-5" />
                    <Input
                    id="whatsapp-number"
                    type="tel"
                    placeholder="Ex: 5511999998888"
                    value={whatsAppNumber}
                    onChange={(e) => setWhatsAppNumber(e.target.value)}
                    />
                    <Button size="icon" onClick={() => updateWhatsAppConfigInFirestore(whatsAppNumber)}>
                    <Save className="w-5 h-5" />
                    </Button>
                </div>
                </CardContent>
            </Card>
            <Card className="shadow-lg">
                <CardHeader>
                <CardTitle>Configurar Escala de Oração</CardTitle>
                </CardHeader>
                <CardContent className="space-y-6">
                <div className="space-y-2">
                    <Label>1. Escolha o Dia da Oração</Label>
                    <Popover>
                    <PopoverTrigger asChild>
                        <Button
                        variant={'outline'}
                        className={cn('w-full sm:w-[280px] justify-start text-left font-normal',!scheduleDate && 'text-muted-foreground')}
                        >
                        <CalendarIcon className="mr-2 h-4 w-4" />
                        {scheduleDate ? format(new Date(scheduleDate), 'PPP', { locale: ptBR }) : <span>Escolha uma data</span>}
                        </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0">
                        <Calendar
                        mode="single"
                        selected={scheduleDate}
                        onSelect={(date) => setScheduleDate(date || undefined)}
                        initialFocus
                        locale={ptBR}
                        disabled={(day) => isBefore(day, startOfToday())}
                        />
                    </PopoverContent>
                    </Popover>
                </div>
                <div className="space-y-2">
                    <Label>2. Defina o Intervalo de Horários</Label>
                    <div className="flex items-center gap-4">
                    <div className="flex items-center gap-2">
                        <Label htmlFor="start-time">Início</Label>
                        <Input id="start-time" type="number" className="w-20" value={startTime} onChange={(e) => setStartTime(parseInt(e.target.value, 10))} min="0" max="23" />
                        <span>h</span>
                    </div>
                    <div className="flex items-center gap-2">
                        <Label htmlFor="end-time">Fim</Label>
                        <Input id="end-time" type="number" className="w-20" value={endTime} onChange={(e) => setEndTime(parseInt(e.target.value, 10))} min="1" max="24" />
                        <span>h</span>
                    </div>
                    </div>
                </div>
                <div className="space-y-2">
                    <Label>3. Ações da Escala</Label>
                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-2">
                    <Button onClick={handleAdminConfigSubmit}>
                        {isScheduleDefined ? "Atualizar Agenda" : "Salvar e Disponibilizar Agenda"}
                    </Button>
                    {isScheduleDefined && (
                        <Button onClick={() => setIsAdminMode(false)} variant="outline" className="bg-green-600 text-white hover:bg-accent hover:text-accent-foreground">
                            Voltar para Escala
                        </Button>
                    )}
                    {allSlotsBooked && (
                        <Button onClick={() => { handleSendToWhatsApp(); toast({ title: "Escala Enviada!", description: "A escala foi enviada para o WhatsApp."}); }} className="w-full flex items-center gap-2" variant="outline">
                            <WhatsappIcon className="w-5 h-5" />
                            Reenviar Escala para o WhatsApp
                        </Button>
                    )}
                    {isScheduleDefined && (
                        <AlertDialog open={isDeleteDialogOpen} onOpenChange={setIsDeleteDialogOpen}>
                        <AlertDialogTrigger asChild>
                            <Button variant="destructive" className="w-full flex items-center gap-2">
                            <Trash2 className="w-5 h-5" />
                            Excluir Escala
                            </Button>
                        </AlertDialogTrigger>
                        <AlertDialogContent>
                            <AlertDialogHeader>
                            <AlertDialogTitle>Você tem certeza?</AlertDialogTitle>
                            <AlertDialogDescription>
                                Esta ação não pode ser desfeita. Isso irá apagar permanentemente a escala atual e todos os agendamentos feitos.
                            </AlertDialogDescription>
                            </AlertDialogHeader>
                            <AlertDialogFooter>
                            <AlertDialogCancel>Cancelar</AlertDialogCancel>
                            <AlertDialogAction onClick={handleDeleteSchedule}>Sim, Excluir</AlertDialogAction>
                            </AlertDialogFooter>
                        </AlertDialogContent>
                        </AlertDialog>
                    )}
                    </div>
                </div>
                </CardContent>
            </Card>
            {isScheduleDefined && bookedSlots.length > 0 && (
                <Card className="shadow-lg">
                <CardHeader>
                    <CardTitle>Gerenciar Agendamentos</CardTitle>
                    <CardDescription>Edite ou remova os agendamentos feitos pelos membros.</CardDescription>
                </CardHeader>
                <CardContent>
                    <Table>
                    <TableHeader>
                        <TableRow>
                        <TableHead>Horário</TableHead>
                        <TableHead>Nome do Membro</TableHead>
                        <TableHead className="text-right">Ações</TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody>
                        {bookedSlots.map((slot) => (
                        <TableRow key={slot.time}>
                            <TableCell className="font-medium">{slot.time}</TableCell>
                            <TableCell>{slot.bookedBy}</TableCell>
                            <TableCell className="text-right space-x-2">
                            <Button variant="outline" size="icon" onClick={() => handleEditSlot(slot)}>
                                <Edit className="w-4 h-4" />
                            </Button>
                            <Button variant="destructive" size="icon" onClick={() => handleFreeSlot(slot)}>
                                <XCircle className="w-4 h-4" />
                            </Button>
                            </TableCell>
                        </TableRow>
                        ))}
                    </TableBody>
                    </Table>
                </CardContent>
                </Card>
            )}
        </div>
      ) : isScheduleDefined ? (
        // User View when schedule is defined
        <div className="space-y-8">
          <Card className="shadow-lg">
            <CardHeader>
              <CardTitle className="flex items-center justify-between">
                <div className='flex items-center gap-2'>
                  <Clock className="w-6 h-6" />
                  Horários para {scheduleDate ? format(new Date(scheduleDate), 'PPP', { locale: ptBR }) : ''}
                </div>
                <Button variant="ghost" size="sm" onClick={handleAdminButtonClick}>
                  Admin
                </Button>
              </CardTitle>
              <CardDescription>
                Clique em um horário vago para agendar sua hora de oração.
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
                {(slots || []).map((slot) => (
                  <Button
                    key={slot.time}
                    variant={slot.isBooked ? 'destructive' : 'default'}
                    className={cn('h-20 flex flex-col items-start p-3 transition-all duration-300 ease-in-out transform hover:scale-105 shadow-md font-bold', !slot.isBooked && 'bg-green-600 hover:bg-green-700 text-white')}
                    onClick={() => handleSelectSlot(slot)}
                    disabled={slot.isBooked}
                  >
                    <div className="text-lg">{slot.time}</div>
                    <div className="flex items-center gap-1 text-sm mt-1 font-normal">
                      {slot.isBooked ? (<><User className="w-4 h-4" /><span>{slot.bookedBy}</span></>) : (<><HelpingHand className="w-4 h-4" /><span>Disponível</span></>)}
                    </div>
                  </Button>
                ))}
              </div>
            </CardContent>
          </Card>
          {bookedSlots.length > 0 && (
            <Card className="shadow-lg">
              <CardHeader>
                <CardTitle>Escala de Oração do Dia</CardTitle>
                <CardDescription>
                  Abaixo está a lista de irmãos comprometidos com a oração para o dia selecionado.
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead className="w-[150px]">Horário</TableHead>
                      <TableHead>Nome do Membro</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {bookedSlots.map((slot) => (
                      <TableRow key={slot.time}>
                        <TableCell className="font-medium">{slot.time}</TableCell>
                        <TableCell>{slot.bookedBy}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          )}
        </div>
      ) : (
        // User View when no schedule is defined
        <div className="space-y-4 text-center">
            <Alert variant="destructive" className="shadow-lg bg-destructive text-white [&>svg]:text-white">
                <AlertTriangle className="h-4 w-4" />
                <AlertTitle>Aviso</AlertTitle>
                <AlertDescription>
                    <p>A escala para o dia de hoje ainda não foi definida. Por favor, volte mais tarde.</p>
                    <p className="mt-2 italic text-black">"Agora, estarão abertos os meus olhos e atentos os meus ouvidos à oração deste lugar." - 2Crônicas 7:15</p>
                </AlertDescription>
            </Alert>
            <Link href="https://www.ibrnobrasil.com.br" target="_blank" rel="noopener noreferrer" className="inline-flex items-center gap-2 text-sm text-primary hover:underline">
                Visite o site da Igreja
                <ExternalLink className="w-4 h-4" />
            </Link>
            <Card className="shadow-lg text-left">
                <CardHeader>
                <CardTitle className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                    <Lock className="w-6 h-6" />
                    Área do Administrador
                    </div>
                </CardTitle>
                <CardDescription>
                    Acesse para configurar ou editar a escala de oração.
                </CardDescription>
                </CardHeader>
                <CardFooter>
                <Button onClick={handleAdminButtonClick} className="w-full">
                    Acessar como Admin
                </Button>
                </CardFooter>
            </Card>
        </div>
      )}

      {/* Dialogs */}
      <Dialog open={isBookingDialogOpen} onOpenChange={setIsBookingDialogOpen}>
        <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
            <DialogTitle>Agendar Horário de Oração</DialogTitle>
            <DialogDescription>
              Você está agendando o horário das {selectedSlot?.time}. Por favor, insira seu nome para confirmar.
            </DialogDescription>
          </DialogHeader>
          <Form {...bookingForm}>
            <form onSubmit={bookingForm.handleSubmit(handleBookingSubmit)} className="space-y-8 p-4">
              <FormField control={bookingForm.control} name="name"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Seu Nome</FormLabel>
                    <FormControl>
                      <Input placeholder="Digite seu nome completo" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <DialogFooter>
                <Button type="submit" className="bg-green-600 hover:bg-green-700 text-white">Confirmar Agendamento</Button>
              </DialogFooter>
            </form>
          </Form>
        </DialogContent>
      </Dialog>

      <Dialog open={isAuthDialogOpen} onOpenChange={setIsAuthDialogOpen}>
        <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
            <DialogTitle>Acesso Restrito</DialogTitle>
            <DialogDescription>
              Por favor, insira a senha de administrador para continuar.
            </DialogDescription>
          </DialogHeader>
          <Form {...authForm}>
            <form onSubmit={authForm.handleSubmit(handleAdminAuthSubmit)} className="space-y-8 p-4">
              <FormField control={authForm.control} name="password"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Senha</FormLabel>
                    <FormControl>
                      <Input type="password" placeholder="Digite a senha" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <DialogFooter>
                <Button type="submit">Autenticar</Button>
              </DialogFooter>
            </form>
          </Form>
        </DialogContent>
      </Dialog>
      
      <Dialog open={isEditingDialogOpen} onOpenChange={setIsEditingDialogOpen}>
          <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
              <DialogTitle>Editar Agendamento</DialogTitle>
              <DialogDescription>
              Alterando o agendamento para o horário das {editingSlot?.time}.
              </DialogDescription>
          </DialogHeader>
          <Form {...editBookingForm}>
              <form onSubmit={editBookingForm.handleSubmit(handleEditBookingSubmit)} className="space-y-8 p-4">
              <FormField
                  control={editBookingForm.control}
                  name="name"
                  render={({ field }) => (
                  <FormItem>
                      <FormLabel>Nome do Membro</FormLabel>
                      <FormControl>
                      <Input placeholder="Digite o novo nome" {...field} />
                      </FormControl>
                      <FormMessage />
                  </FormItem>
                  )}
              />
              <DialogFooter>
                  <Button type="submit">Salvar Alterações</Button>
              </DialogFooter>
              </form>
          </Form>
          </DialogContent>
      </Dialog>
    </div>
  );
}
